import React,{useState,useEffect} from 'react'
import {useDispatch,useSelector} from 'react-redux'
import Screen1 from './S1Component/Screen1'
import Screen2 from './S2Component/Screen2'
import {useNavigate} from 'react-router-dom';
import {ANIM_RESET} from '../Constants/animConstants'
import './HomeScreen.scss'



// homeScreen 
// Screen1 component 
// Screen2 component 
//  redux pass value
//  navigate contentScreen 

// new plugin
// https://webdevelop.pro/blog/createing-svg-animation-lottie
function HomeScreen() {

    const dispatch = useDispatch()
    const navigate = useNavigate()

    const anim = useSelector(state=>state.anim) // reduecer
    const {Screen1:S1 ,Screen2:S2 ,Screen3:S3 ,S2ToS3Value:S2ToS3} = anim
    //------- selector = setState render useEffect
    // state 


    useEffect(()=>{
        if(S3==true){
            navigate(`/content/${S2ToS3}`)
            dispatch({
                type:ANIM_RESET
            })
        }

    },[ S3,S2ToS3 ])
    // [] state useEffectfunction
    

    return (
        <>
            {S1==true
                ? <Screen1/>
                : null
            }

            {S2==true
                ? <Screen2/>
                : null
            }
    
        </>
    )
}

export default HomeScreen